* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  background-image: url('images/main.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-color: black;
  width: 100vw;
  height: 100vh;
}

.app {
  text-align: center;
}

.app-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.full-name {
  display: flex;
  flex-wrap: wrap;
}

.first-name,
.last-name {
  color: black;
  background-color: #ba3447;
  padding: 0.1rem 1rem;
  font-size: 4rem;
  margin: 0.6rem 0.1rem;
  border-radius: 0.2rem;
}

@media only screen and (max-width: 600px) {
  .first-name, .last-name {
    font-size: 2.5rem;
  }
}

.last-name {
  background-color: #141722;
  color: white;
}

.social-icons {
  background-color: white;
  border-radius: 0.2rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 250px;
  padding: 0.5rem 1rem;
  margin: 0.6rem;
}

.icons {
  color: #ba3447;
}

.resume-request {
  background-color: #ba3447;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 0.2rem;
}
